@import "./reset";
@import "./variable";
@import "./function";
@import "./mixin";
@import "./typography";
@import "./layout";
@import "./list";
@import "./btn";
@import "./banner";
@import "./media";
@import "./input";
@import "./search";
@import "./animation";
@import "./header";
.Modal {
    position: absolute;
    bottom: 0;
    border-top: 1px solid #000;
    border-radius: 0.5rem 0.5rem 0 0;
    width: 100%;
    background: #fff;
    padding: 1rem;
    transition: 0.5s;
    min-height: 90%;
    max-height: 90%;
    overflow: auto;
}
body {
    scroll-behavior: smooth;
}
.Key-module_row-button {
    font-family: inherit;
    font-weight: bold;
    border: 0;
    padding: 10px;
    height: 58px;
    border-radius: 4px;
    width: 100%;
    cursor: pointer;
    user-select: none;
    background-color: #d3d6da;
    color: $primary-color;
    text-transform: uppercase;
}
.keygap {
    min-width: 10px;
}
.jeff {
    width: 50%;
    max-width: 300px;
}
.typeKeyboard input {
    border: 1px solid transparent;
    padding: 10px 20px;
    text-align: center;
    border-bottom: 1px solid $primary-color;
    outline: none;
    width: 100%;
    margin-bottom: 1rem;
}
.StatisticsMain {
    position: absolute;
    bottom: 0;
    z-index: 9;
    padding-top: 1rem;
    border-radius: 5px;
    border: 1px solid $primary-color;
    background: #fff;
    bottom: -300%;
    transition: .7s;
    overflow: auto;
    width: 100%;
    &.StatisticsPage.active {
        bottom: 0%; 
    }
    &.congratulationsm.active {
        bottom: 0%; 
    }

    &-top {
        &-center {
            font-size: 24px;
        }
        &-left {
            cursor: pointer;
            font-size: 20px;
        }
    }
}
.closebutton {
    font-size: 1.2rem;
}
.section10  {
    position: absolute;
    bottom: 0;
    z-index: 9;
    padding: 1rem;
    border-radius: 2rem;
    border: 1px solid $primary-color;
    background: #fff;
    bottom: -300%;
    transition: .7s;
    overflow: auto;
    width: 100%;
    &.active {
        bottom: 0;
    }
    h2 {
        font-size: 1.4rem;
    }
    &_img {
        border: 1px solid #000;
    } 
    &_Box {
        width: 4rem;
        height: 4rem;
        border-radius: 50%;
        background: #D9D9D9;
    }
    .forst {
        border-bottom: 1px solid #000;
    }
}

.result {
    width: 70%;
    margin: 1rem auto;
    &Box {
        &__top {
            font-size: 40px;
        }
        &__bottom {
            font-size: 10px;
        }
       
    }

}


.section1 {
    width: 90%;
    margin: 0 auto 1rem;
    &Box {
      padding: 1rem 2rem;
    font-size: 20px;
    border-top: 1px solid $primary-color;
    border-bottom: 1px solid $primary-color;
    &:last-child{
        border-bottom: unset;
    }
  }
    a {

        color: #1565FF;
    }
}

.flowBox {
    padding: 1rem;
    h2 {
        font-weight: 400;
font-size: 20px;

    }
}
.Setting {
    position: absolute;
    bottom: 0;
    border-top: 1px solid $primary-color;
    border-radius: .5rem .5rem 0 0;
    width: 100%;
    background: #fff;
    padding: 1rem;
    transition: .5s;
    bottom: -100vh;
    &.active {
        bottom: 0;
    }

}
.userList {
    li {
      & + li {
        border-top: 1px solid $primary-color;
      }  
    }
}
.playBox {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    border: 1px solid $primary-color;
    cursor: pointer;
    i {
        font-size: 2rem
    }
}
.heading {
    &-left {
        cursor: pointer;
    }
    &-right {
        position: relative;
        left: -1rem;
        font-weight: 500;
    }
}
.Modal {
    &__heading {
        &--left {
            cursor: pointer;
        }
        &--right {
            position: relative;
            left: -1rem;
            font-weight: 500;
        }
    }
   
}
.timeMian {
    h2 {
        font-size: 40px;
    }
}
.border-t {
    border-color: #000
}
.faqBox {
    h2 {
        font-size: 22px
    }
}
.inputdesign {
    outline: none;
    padding: 20px;
    border-bottom: 1px solid #000;
}
.w-100 {
    width: 100%;
}
.colorMain {
    &Box {
     &_ {
        width: 12px;
        height: 12px;
        border-radius: 50%;
     }
    }
}
.size {
    .sizeBox{
        background: #ddd;
        border-radius: 50%;
&:nth-child(1){
width: 30px;
height: 30px;
}        
&:nth-child(2){
width: 25px;
height: 25px;
}        
&:nth-child(3){
width: 20px;
height: 20px;
}        
&:nth-child(4){
width: 10px;
height: 10px;
}        

&.active {

            background: #000;
        }
    }
}
