}
.inputMain  {
margin-bottom: 1rem;
}
.inputMainBox {
    margin-top: 5px;
    input {
        outline: none;
        border-radius: .4rem;
        padding: 10px 2rem;
        width: 100%;
        background: #D9D9D9;
        border: 1px solid #000;
    }
}

.dashboard {
    max-width: 100%;
    margin: 0;
    border: transparent;
}
.loginForm {
    width: 50%;
    margin: 5rem auto;
}
.adminheading {
    padding: 1rem ;
    font-size: 1.3rem;
    border: 1px solid #000;
}
.adminlist {
    max-height: 300px;
    overflow: auto;
    li {
        padding: 10px;
        font-size: 14px;
        .heart {
            width: 30px;
            height: 30px;
            background: #fff;
            border-radius: 50%;
            box-shadow:0 0 2px #000;
            display: grid;
            place-items: center;
        }
        .close {
            svg {
                font-size: 1.2rem;
            }
        }
        span {
            line-height: 1.1;
            display: block;
        }
        &:nth-child(2n) {
            background: #D9D9D9;
           
        }
    }
}
.paintBox {
    width: 100%;
    height: 300px;
    background: #ddd;
    border: 1px solid #000;
}
.aadmindasboard__center {
    &- {
        .timeBox {

            width: 40px;
            height: 40px;
            background: #D9D9D9;
            padding: 10px;
            border-radius: 50%;
        }
        &right {
            h4 {
                font-size: .9rem;
            }
            h5 {
                font-size: 1.2rem;
            }
        }
    }
}
